.main {
  height: 100%;
  &__header {
    padding-bottom: 20px;
    display: flex;
    justify-content: space-between;

    &__title {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &__back {
        border-radius: 5px;
        height: 40px;
        width: 40px;
        margin-right: 20px;
        background: $primary;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        padding-left: 5px;

        .material-icons {
          font-size: 16px;
        }
        @include clickable();
      }
    }

    &__btn {
      display: flex;
      justify-content: flex-end;

      &__item {
        &:first-child {
          margin-right: 10px;
        }

        &--fluxo {
          width: 320px;
          height: 40px;
          border-radius: 4.5px;
          border: solid 1px #bcbfd0;
        }
      }
    }
  }

  &__body {
    flex-direction: column;
    // align-items: center;
    justify-content: center;
    width: 100%;

    &__empty-state {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 70vh;

      &__img {
        animation: upAndDown 1s linear infinite;
        animation-duration: 3s;
        img,
        svg {
          width: 275px;
        }
      }

      &__btn {
        animation: upAndDown 1s linear infinite;
        animation-duration: 3s;
        margin-top: 20px;
      }
    }
  }

  .hidden {
    display: none;
  }

  .visible {
    display: grid;
  }
}

.constructor {
  background: #f5f7ff;
  display: none;
  position: relative;
  height: calc(100vh - 93px);
  justify-content: space-between;

  &__content {
    display: none;
    flex-direction: column;
    position: relative;
    &__map {
      display: flex;
      align-items: center;

      margin-top: 20px;
      .list-drop__close__icon {
        margin-top: 4px;
        svg {
          use {
            fill: #828292;
          }

          transform: rotate(272deg);
        }
      }

      .constructor__content__map__text {
        font-size: 12px;
        font-weight: 600;
        color: #828292;
      }
    }

    .btn-add-block {
      display: flex;
      align-items: center;
      max-width: 151px;
      height: 40px;
      font-size: 14px;
      margin-top: 10px;
    }

    &__btn {
      position: relative;
      display: flex;
      align-items: center;
      color: $dark-3 !important;
      justify-content: space-between;

      .btn-icon {
        margin-top: 5px;
      }
    }

    &.active {
      display: flex;
    }
  }

  &__nav {
    display: flex;
    width: 100%;
    justify-content: center;
    height: 40px;
    margin-top: 21px;

    &__item {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 87px;
      font-size: 14px;
      margin-right: 8px;
      border-radius: 5px;
      color: #828292;
      cursor: pointer;
      height: 40px;
      &:last-child {
        margin-right: 0;
      }

      &.active {
        border: solid 2px #455cc7;
        color: #455cc7;
      }
    }
  }

  &__param-header {
    display: none;
    flex-direction: column;
    width: 100%;
    margin-top: 30px;

    &__content {
      display: flex;
      align-items: center;
      // justify-content: space-between;

      &__number {
        color: #828292;
        font-weight: 600;
        margin-right: 14px;
      }

      &__close {
        margin: 0 10px 0 3px;
        cursor: pointer;
        i {
          display: flex;
          font-size: 18px;
          color: #adb1c9;
        }
      }
      &__inputs {
        width: 100%;
        display: flex;
        height: 40px;
        border: 2px solid #bcbfd0;
        border-radius: 5px;

        input {
          font-family: "Inter Regular";
          width: 50%;
          border: none;
          padding: 0 8px;
          color: #bcbfd0;
          &:last-of-type {
            border-left: 1px solid #bcbfd0;
          }
        }
      }
    }
    &.active {
      display: flex;
    }
  }

  &__param-body {
    margin-top: 30px;

    display: none;
    flex-direction: column;

    &.active {
      display: flex;
    }
  }
  &__param-params {
    margin-top: 30px;

    display: none;
    flex-direction: column;

    &.active {
      display: flex;
    }
  }

  &__menu {
    position: absolute;
    z-index: 2;
  }
  &__content-menu {
    display: flex;

    &__menu {
      display: none;
      align-items: center;
      width: 20px;
      justify-content: center;
      align-self: center;
      background-color: #fff;
      height: calc(100vh - 93px);
      border-right: 1px solid $light-5;
      cursor: pointer;

      &.active {
        display: flex;
      }

      &__items {
        &__item {
          width: 4px;
          height: 20px;
          border-radius: 3px;
          background: $light-5;
        }
      }
    }

    &__menu-open {
      display: none;
      width: 40px;
      background-color: #fff;
      display: none;
      height: calc(100vh - 93px);
      justify-content: center;
      border-right: 1px solid $light-5;
      z-index: 1;

      &__items {
        &__item {
          &.tree {
            right: 0;
          }
          padding-top: 16px;

          cursor: pointer;
          svg {
            path {
              fill: #8c8c9a;
            }
          }

          &.active,
          &:hover {
            svg {
              path {
                fill: $primary;
              }
            }
          }

          @include clickable();
        }
      }
      &.active {
        display: flex;
      }
    }
  }

  .title {
    font-family: "Inter Bold";
    font-size: 24px;
    .title-img {
      display: flex;
      margin-top: 4px;
      font-size: 10px;
      align-items: center;
      font-weight: 500;
      border-radius: 5px;
      border: solid 1px #e2e2ec;
      padding: 2px 6px;
      background-color: #fbfbff;
      text-transform: uppercase;
      width: 121px;
      img {
        width: 16px;
        height: 16px;
        margin-right: 3px;
      }
    }
  }

  .container-blocks {
    position: relative;
    height: calc(100vh - 93px);
    display: none;
    font-family: "Inter Regular";

    &.active {
      display: flex;
    }

    .sub-title {
      font-size: 16px;
      margin-bottom: 10px;
      color: $dark-3;
    }
    .nav {
      width: 344px;
      height: calc(100vh - 93px);
      display: block;
      position: absolute;
      border-right: 1px solid $light-5;
      background: #fff;
      padding: 28px 22px;
      left: -9999px;
      transition: all 0.5s;

      .header-block {
        display: flex;
      }

      &--block {
        padding: 0;

        .header-block {
          padding: 28px 22px;

          .title {
            font-family: "Inter Bold";
            font-size: 24px;
          }
        }

        .block-content {
          padding: 0 28px;
          display: flex;
          height: 56px;
          align-items: center;
          cursor: pointer;

          &:hover {
            background-color: $light-6;
          }

          width: 100%;
          &__icons {
            display: flex;
            &__icon {
              margin-right: 12px;
            }
          }
          &__text {
            font-size: 16px;
          }
        }

        .search {
          margin: 16px 22px;
        }
      }

      &--variable {
        padding: 0;

        .header-variable {
          padding: 28px 22px;
          @include borderbottom();
        }

        .variable__creating {
          padding: 27px 16px;
          @include borderbottom();

          &__area-input {
            display: flex;
            align-items: center;
            height: 40px;
            border: solid 1px $light-2;
            border-radius: 5px;
            padding: 0 14px;
            color: $dark-3;
            margin-bottom: 10px;
            &__icon {
              margin-right: 8px;
              margin-top: 3px;

              svg {
                width: 20px;
                height: 20px;
                path {
                  fill: $primary;
                }
              }
            }
            &__input {
              font-family: "Inter Regular";
              border: none;
              font-weight: normal;
            }
          }

          &__text {
            font-size: 10px;
            color: $dark-3;
            span {
              font-weight: bold;
            }
          }
        }

        .variable__created {
          padding: 27px 16px;

          .sub-title {
            display: flex;
            align-items: center;
            justify-content: space-between;

            &__btn-menu {
              cursor: pointer;
              &:hover {
                svg {
                  path {
                    opacity: 0.8;
                  }
                }
              }
            }
          }

          &__items {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(133px, 0.5fr));
            grid-gap: 10px;
            color: $dark-2;
            &__item {
              display: flex;
              align-items: center;
              min-width: 133px;
              height: 40px;
              border-radius: 5px;
              border: solid 1px #e2e2ec;
              background-color: #fbfbff;
              font-size: 14px;
              word-break: break-all;

              cursor: pointer;

              i {
                font-size: 18px;
                margin-top: 2px;
                margin-right: 8px;
                margin-left: 14px;

                @include clickable();
              }
              span {
                color: $primary;
              }
            }
          }
        }
      }

      &--tree {
      }

      &--linear {
      }

      &.active {
        left: 0;
      }
    }

    .search {
      margin: 16px 0;
    }

    .opener {
      align-self: center;
      width: 4px;
      height: 20px;
      display: flex;
      border-radius: 3px;
      cursor: pointer;
      position: absolute;
      left: 342px;
      padding: 10px;

      img {
        width: 10px;
        height: 20px;
        object-fit: contain;
      }
    }
  }

  &__container-start {
    border-left: 1px solid $light-5;
    height: calc(100vh - 100px);
    width: 369px;
    background-color: #fff;
    z-index: 1;
    font-family: "Inter Regular";
    display: block;
    position: absolute;
    overflow-y: auto;
    right: -4000px;
    overflow-x: hidden;
    transition: 0.3s all;
    &.active {
      display: block;
    }

    &__header {
      display: flex;
      justify-content: space-between;
      padding: 28px 16px 26px 16px;
      @include borderbottom();

      &__actions {
        display: flex;
        .icon {
          margin-left: 19px;
        }
      }
    }
    &__main {
      padding: 21px 16px 21px 16px;
      display: flex;
      flex-direction: column;
      @include borderbottom();

      &__textarea {
        font-family: "Inter Regular";
        font-size: 14px;
        line-height: 19px;
        width: 100%;
        height: 68px;
        border-radius: 4px;
        border: solid 1px #bcbfd0;
        resize: none;
        padding: 10px 12px;
        color: $dark-4;
      }

      &__text {
        font-family: "Inter bold";
        font-size: 10px;
        letter-spacing: 0;
        margin: 3px 0 0 0;
      }
    }

    &__content {
      padding: 18px 16px 24px 16px;
      @include borderbottom();

      &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &__text {
          font-size: 12px;
          color: $dark-2;
        }
      }

      &__main {
        display: flex;
        justify-content: center;

        .btn-add-answer {
          position: relative;
          font-family: "Inter Regular";
          display: flex;
          align-items: center;
          padding: 0 14px;
          color: $dark-2;
          font-size: 14px;
          width: 191px;
          height: 40px;
          border-radius: 5px;
          border: solid 1px #e2e2ec;
          background-color: #fbfbff;
          margin-top: 12px;
          @include clickable();

          svg {
            margin-right: 8px;
            width: 20px;
            height: 20px;
          }
        }
        &--column {
          flex-direction: column;
        }
      }
      &:last-of-type {
        border-bottom: 0;
      }
    }

    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 26px;
      height: 26px;
      border-radius: 50%;
      margin-left: 20px;
      border: solid 1px $light-5;
      @include clickable();
      object-fit: contain;

      svg {
        width: 20px;
        height: 20px;
      }

      &--icon-small {
        width: 19px;
        height: 19px;
        svg {
          width: 15px;
          height: 15px;
        }
      }
    }

    &__input {
      input {
        font-family: "Inter Regular";
        color: #202126;
        width: 100%;
        padding: 8px 12px;
        border-radius: 4px;
        border: #bcbfd0 1px solid;
        margin-bottom: 9px;
      }
    }
    &__add-answer {
      display: none;
      width: 338px;
      border-radius: 4px;
      box-shadow: 0 3px 15px 0 rgba(32, 33, 38, 0.12);
      padding: 0 12px;
      padding-bottom: 14px;
      margin-top: 16px;
      position: relative;

      &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 14px 0px;

        &__number {
          font-size: 12px;
          color: #828292;
          border-radius: 10px;
          border: 2px solid #f8f9ff;
          text-align: center;
          width: 20px;
          line-height: 18px;
          padding-right: 1px;
          height: 20px;
        }
        &__close {
          cursor: pointer;
          i {
            font-size: 16px;
            display: flex;
          }
        }
      }

      &.active {
        display: block;
      }
    }
    &__conect {
      &__header {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        font-size: 10px;

        color: $dark-3;
        i {
          display: flex;
          font-size: 14px;
          margin-right: 6px;
        }
      }

      &__select {
        display: flex;
        justify-content: space-between;
        font-size: 14px;
        color: #bcbfd0;
        border-radius: 4px;
        border: solid 1px #bcbfd0;
        align-items: center;
        padding: 8px 12px;
        cursor: pointer;
        position: relative;
        transition: all 0.5s;

        &__icon {
          i {
            display: flex;
          }
        }

        &__options {
          // left: calc(50% - 156px);
          height: 0;
          opacity: 0;
          position: absolute;
          width: 100%;
          background: #fff;
          padding: 10px 18px;
          box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
          transition: all 0.3s;
          z-index: 9;
          top: 148px;
          width: 314px;
          margin-bottom: 15px;

          &__header {
            display: none;
            align-items: center;
            &__item {
              margin-right: 14px;
              font-size: 12px;
              cursor: pointer;
              color: #828292;
              font-weight: 400;

              &:hover {
                font-weight: 600;
              }
              &.active {
                color: $primary;
                font-weight: 600;
              }
            }
          }
          &__content-fluxos {
            display: none;
            flex-direction: column;

            &__empty {
              display: none;
              padding: 32px 0;
              align-self: center;
              color: #202126;

              &.active {
                display: block;
              }
            }

            &.active {
              display: flex;
            }
          }

          &__content-blocks {
            display: none;
            flex-direction: column;
            &__empty {
              display: none;
              padding: 32px 0;
              align-self: center;
              color: #202126;

              &.active {
                display: block;
              }
            }

            &__add-block {
              align-self: center;
              padding: 25px 0;
              z-index: 9;
              pointer-events: visible;
            }

            &.active {
              display: flex;
            }
          }

          &.active {
            opacity: 1;
            height: auto;

            .constructor__container-start__conect__select__options__header {
              display: flex;
            }
          }
        }
        &.active {
          .constructor__container-start__conect__select__icon {
            transform: rotate(180deg);
          }
        }
      }
    }

    &.active {
      right: 0;
    }
  }

  &__add-box {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    border: 2px solid $primary;
    position: absolute;
    right: 37px;
    top: 37px;
    background: #fff;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      width: 24px;
      path {
        fill: $primary;
      }
    }
  }

  .subtitle-conf {
    display: flex;
    justify-content: space-between;
  }

  .btn--secondary {
    display: flex;
    align-self: flex-end;
  }

  &.active {
    display: flex;
  }
}

.notification {
  padding: 10px 14px;
  width: 100%;
  color: #fff;
  border-radius: 4px;
  font-size: 10px;
  font-family: "Inter SemiBold";
  display: none;
  justify-content: flex-start;
  align-items: center;
  white-space: nowrap;

  svg {
    width: 14px;
    height: 14px;
    margin-right: 5px;
    path {
      fill: #fff;
    }
  }

  &.warning {
    background-color: #ffbb00;
    width: 100%;
  }

  &.error {
    background-color: #f7404e;
  }

  &.success {
    background-color: #45c777;
  }

  &.active {
    display: flex;
  }
}
.notification-builder {
  position: relative;
  border-radius: 4px;
  background-color: #e9eaef;
  padding: 6px 14px;
  color: #3e3f44;
  display: none;
  justify-content: space-between;
  align-items: center;
  min-width: 306px;

  &__main {
    &__title {
      font-size: 11px;
    }
    &__text {
      font-size: 9px;
    }
  }

  &__icon {
    display: flex;
    align-items: center;
    svg {
      width: 18px;
      height: 18px;
    }
  }

  &.warning {
    .notification-builder__icon {
      svg {
        path {
          fill: #ffbb00;
        }
      }
    }
  }

  &.error {
    .notification-builder__icon {
      svg {
        path {
          fill: #f7404e;
        }
      }
    }
  }

  &.success {
    .notification-builder__icon {
      svg {
        path {
          fill: #45c777;
        }
      }
    }
  }

  .notification {
    position: absolute;
    right: 0;
    top: 42px;
    width: auto;
  }
  &.active {
    display: flex;
  }
}

.input-dashed {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  margin-top: 16px;
  border: 1px dashed #d9dae1;
  height: 40px;
  border-radius: 5px;
  input {
    border: none;
    width: 100%;
    font-family: "Inter Regular";
    display: flex;
    background: #fff;
    font-size: 14px;
    padding: 0px 14px;
    color: $dark-3;
    transition: 0.5s all;
  }
  ::-webkit-input-placeholder {
    color: #d9dae1;
  }

  &__icon {
    margin-right: 5px;
    cursor: pointer;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      width: 20px;
      height: 20px;
    }
  }
}
.btn-add-object {
  font-family: "Inter Regular";
  display: flex;
  align-items: center;
  border: 1px dashed #d9dae1;
  background: #fff;
  height: 40px;
  font-size: 14px;
  border-radius: 5px;
  padding: 0px 14px;
  cursor: pointer;
  color: $dark-2;
  margin-top: 16px;
  transition: 0.5s all;
  i {
    font-size: 18px;
    margin-top: 2px;
  }

  &--small {
    max-width: 120px;
  }

  // &--medium {
  //   max-width: 151px;
  // }
}

.container-select {
  position: relative;
  width: 100%;

  .select-btn {
    background-color: white;
    border: 1px solid #b7bec5;
    height: 51px;
    width: 100%;
    font-family: "Inter Regular";
    font-size: 14px;
    border-radius: 5px;
    color: #b7bec5;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    text-align: left;
    padding-left: 10px;
    padding-right: 10px;
    -webkit-box-pack: justify;
    justify-content: space-between;
    cursor: pointer;
    border: dashed 1px #b7bec5;
    height: 40px;
    margin-top: 10px;

    .title {
      font-family: "Inter Regular";
      font-size: 14px;
      color: #828292;
    }

    i {
      transform: rotate(90deg);
      transition: all 0.1s;
    }

    &.active {
      i {
        transform: rotate(-92deg);
      }
    }

    &--integrations {
      color: #3e3f44;
    }
  }
}

.list-drop {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  &__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    font-weight: 500;
    margin-bottom: 16px;
    letter-spacing: 0.2px;
    color: #828292;
  }
  &__item {
    img {
      margin-right: 14px;
      width: 18px;
      height: 18px;
    }
    svg {
      margin-right: 14px;
      width: 18px;
      height: 18px;
    }
    a {
      display: flex;
      align-items: center;
      color: $dark-3;
      font-size: 14px;
      font-weight: 600;
    }
    &:last-child {
      margin-bottom: 0;
      border-radius: 0px 0px 5px 5px;
    }

    &:hover {
      background: #f5f7ff;
    }
  }

  .folder {
    display: flex;
    align-items: center;
    color: #828292;
    // padding-bottom: 8px;
    cursor: pointer;
    &:hover {
      background: #ccc;
    }

    &__name {
      font-size: 14px;
    }
    &__icon {
      display: flex;
      align-items: center;
      svg {
        margin-right: 10px;
        path {
          fill: #828292;
        }
      }
    }
  }

  &--primary {
    display: none;
    border-radius: 5px;
    box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
    position: absolute;
    width: 100%;
    z-index: 5;
    background: #fff;
    opacity: 0;
    transition: 0.2s all;
    .list-drop__item {
      cursor: pointer;
      height: 48px;
    }

    &.active {
      display: flex;
      .list-drop__item {
        display: flex;
      }
      height: auto;
      opacity: 1;
      pointer-events: all;
    }
  }

  &--secondary {
    display: flex;
    border-radius: 5px;
    box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
    position: absolute;
    width: 100%;
    z-index: 5;
    background: #fff;
    padding: 10px;
    opacity: 0;
    height: 0px;
    margin-bottom: 20px;
    overflow-y: auto;
    transition: 0.1s all;
    ul {
      margin: 0;
      padding: 0;
      li {
        padding-left: 10px;
      }
    }

    .folder {
      .wrapper-content-folder {
        display: flex;
        align-items: center;
        padding: 10px 0;
      }
      &:hover {
        background: #fff;
      }

      &--column {
        flex-direction: column;
        align-items: flex-start;
      }
    }

    .list-name-folder {
      cursor: pointer;
    }
    .list-drop__item {
      cursor: pointer;
      &__name-folder {
        font-weight: 400;
        font-size: 14px;
      }
      &__img {
        width: 22px;
        height: 22px;
        margin-right: 20px;
        img {
          width: 22px;
          height: 22px;
        }
      }
      &:hover {
        background: #fff;
      }
    }

    &.active {
      height: 300px;
      opacity: 1;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    padding: 0 14px;

    &__sub-item {
      display: flex;
      margin-left: 20px;
    }

    &--column {
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
    }

    &__primary,
    &__secondary {
      display: flex;
      width: 100%;
      padding: 10px;
    }
  }

  &--constructor-block {
    display: none;

    .list-drop__item {
      height: 48px;
    }

    .list-drop__close {
      i {
        font-size: 16px;
        display: flex;
        color: $dark-3;
      }
      // margin: 5px 0;
      cursor: pointer;
    }

    &.active {
      display: flex;
    }
  }

  &--create-box {
    display: none;
    transition: all 0.3s;
    position: absolute;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
    right: 0;

    .list-drop__item {
      height: 48px;
      padding: 14px;
      svg {
        margin-right: 14px;
        width: 18px;
        height: 18px;
        path {
          fill: #000;
        }
      }
      a {
        display: flex;
        align-items: center;
        color: $dark-3;
        font-size: 14px;
        font-weight: 600;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    &.active {
      display: block;
      top: 45px;
    }
  }
}

.map-var {
  &__response {
    display: none;

    &.active {
      display: flex;
    }
  }
  &__dataset {
    display: none;

    &__content {
      width: 100%;
      border-radius: 4px;
      border: solid 1px #e2e2ec;
      display: flex;
      flex-direction: column;
      &__line {
        display: flex;

        border-bottom: solid 1px #e2e2ec;
        height: 23px;
        color: #3e3f44;
        font-size: 10px;
        align-items: center;
        padding: 0 4px;
        cursor: pointer;

        img {
          width: 12px;
          height: 12px;
          object-fit: cover;
          margin-right: 2px;
        }
      }

      .container-datasets {
        width: 100%;
        margin: 0;
        padding: 0;
        .title {
          font-family: "Inter Regular" !important;
          display: flex;
          align-items: center;
          color: #3e3f44 !important;
          font-size: 10px !important;
          cursor: pointer;

          img {
            width: 12px;
            height: 12px;
            object-fit: cover;
            margin-right: 4px;
          }
        }
        .dataset-item {
          color: #3e3f44;
          cursor: pointer;

          

          &.active {
            font-weight: 600;
          }

          ul {
            display: none;
          }

          &.show {
            .title {
              font-weight: 600;
            }
            ul {
              display: block;

              li {
                padding-left: 20px;
                padding-top: 4px;
                padding-bottom: 4px;
              }
            }
          }
          &.hide {
            .title {
              font-weight: 400;
              img {
                transform: rotate(-90deg);
              }
            }
            ul {
              display: none;
            }
          }

          // &:hover {
          //   background: #f5f7ff;
          // }
        }

        li {
          width: 100%;
          padding: 2px 5px;
          font-size: 10px;
          color: #3e3f44;
        }
      }
    }

    &.active {
      display: flex;
    }
  }

  &__content {
    display: flex;
    width: 100%;

    pre {
      background: #f5f7ff;
      padding: 11px 10px;
      border-radius: 0px 4px 4px 0px;
      white-space: pre-wrap;
      word-break: break-word;
    }
  }
}

.change-mode {
  display: flex;
  border-radius: 5px;
  border: solid 1px $primary;
  margin-right: 54px;
  justify-content: space-between;

  &__icon {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $primary;
    height: 40px;
    width: 40px;

    cursor: pointer;
    svg {
      height: 24px;
      width: 24px;
      path {
        fill: $primary;
      }
    }

    &.active {
      background: $primary;
      svg {
        path {
          fill: #fff;
        }
      }
    }
  }
}

.container-drag-drop {
  display: none;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 16px;
  background: #f5f7ff;
  padding: 39px 0;
  border-radius: 5px;
  width: 100%;
  border: dashed 1px #d9dae1;

  &__text {
    font-size: 14px;
    font-weight: bold;
    text-align: center;
    color: #828292;
    margin-bottom: 15px;
  }

  button {
    font-size: 14px;
  }

  &.active {
    display: flex;
  }
}

.change-social-social {
  display: flex;
  border-radius: 5px;
  box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
  border: solid 1px #f8f9ff;
  height: 40px;
  align-items: center;
  justify-content: center;
  justify-self: center;

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 64px;
    height: 36px;
    border-radius: 3px;
    cursor: pointer;
    font-size: 12px;
    font-weight: bold;
    line-height: 1.5;
    letter-spacing: 0.14px;
    border-left: 1px solid #f5f7ff;
    transition: all 0.3s;
    overflow: hidden;

    img {
      width: 20px;
      height: 20px;
      // object-fit: cover;
      transition: all 0.3s;
    }

    &__text {
      display: none;
      padding-left: 5px;
      color: #3e3f44;
    }

    &.active {
      border: none;
      background-color: #fbfbff;
      width: 114px;
      justify-content: center;
      padding: 0 16px;
      .change-social-social__item__text {
        display: block;
      }
    }
  }
  .wpp {
    &.active {
      background: rgba(50, 215, 75, 0.1);
    }
  }
  .wbc {
    &.active {
      background: rgba(255, 154, 0, 0.1);
    }
  }
  .voice {
    &.active {
      background: rgba(19, 19, 22, 0.1);
    }
  }
  .msgr {
    &.active {
      background: rgba(33, 150, 243, 0.1);
    }
  }
}

.aba-help {
  position: fixed;
  padding: 13px 14px;
  right: -990px;
  bottom: 0;
  height: 100vh;
  z-index: 9;
  width: 345px;
  background: #fff;
  box-shadow: 0 25px 26px 1px rgba(19, 19, 22, 0.12);
  transition: all 0.3s;
  color: #3e3f44;
  &__title {
    font-family: "Inter SemiBold";
    font-size: 20px;
    border-bottom: 1px solid #d9dae1;
    padding-bottom: 22px;
    display: flex;
    justify-content: space-between;
    .icon-Close {
      cursor: pointer;
    }
  }

  &__search {
    margin-top: 21px;
    .title {
      font-family: "Inter SemiBold";
      font-size: 16px;
      margin-bottom: 11px;
    }
  }

  &__main {
    max-height: calc(100% - 205px);
    overflow-y: auto;
    margin: 21px 0 60px 0;
    .title {
      font-size: 16px;
      font-family: "Inter SemiBold";
    }

    &__top-topics {
      margin-bottom: 15px;
      &__item {
        display: flex;
        align-items: center;
        padding: 10px 0;
        border-bottom: 1px solid #d9dae1;
        &__icon {
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 5px;
          border-radius: 4px;
          border: solid 1px #bcbfd0;
          margin-right: 12px;

          svg {
            width: 20px;
            height: 20px;
            path {
              fill: #bcbfd0;
            }
          }
        }

        a {
          color: #3e3f44;
        }
      }

      .btn {
        position: relative;
        margin: 10px 0;
        padding: 0;
        border: none;
        background-color: transparent;
        color: $primary;
        &:visited {
          color: $primary;
        }
      }
    }

    &__glossary {
      .title {
        margin-bottom: 18px;
      }
      &__item {
        margin-bottom: 19px;
        .title {
          font-family: "Inter Bold";
          font-size: 14px;
          margin: 0;
        }

        .text-ellipsis {
          max-width: 308px;
          font-family: "Inter Regular";
          font-size: 14px;
          margin: 10px 0;
        }

        a {
          margin: 10px 0;
          padding: 0;
          border: none;
          color: $primary;
        }
      }
    }
  }

  &__footer {
    display: flex;
    border-top: solid 1px #e2e2ec;
    justify-content: center;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    background: #fff;
  }

  &.active {
    right: 0;
  }
}

.circle-conector {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: dashed 1px #d9dae1;

  .mask{
    display: none;
    height: 8px;
    width: 8px;
    border-radius: 50%;
    background-color: #455cc7;

    &.active{
      display: block;
    }
  }
}

@keyframes upAndDown {
  0%,
  100% {
    margin-bottom: 20px;
  }
  50% {
    margin-bottom: 0px;
  }
}
