.login {
  background: #fff;
  &__wrapper {
    display: grid;
    grid-template-columns: 391px 1fr;
    height: 100vh;

    &__col1 {
      padding: 20px 26px;
      border-right: 1px solid #d9dae1;
      position: relative;

      a, .forgotLink {
        font-family: "Inter Bold";
        font-size: 12px;
        letter-spacing: 0.2px;
        color: $primary;
        cursor: pointer;
      }
      .text {
        color: #202126;
        font-size: 14px;
        margin-top: 10px;
      }

      .header-login {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .logo-login {
          img {
            width: 39px;
            height: 45px;
            object-fit: contain;
          }
        }
      }

      .form-login {
        margin-top: 77px;
        .title {
          font-family: "Inter Bold";
          font-size: 24px;
          letter-spacing: 0.24px;
          color: #3e3f44;
        }

        #email,
        #resend-email {
          margin-top: 16px;
        }
        .container-inputs {
          display: flex;
          flex-direction: column;
          margin-top: 31px;

          label {
            font-family: "Inter Medium";
            font-size: 12px;
            line-height: normal;
            letter-spacing: 0.2px;
            color: #828292;
            // margin-bottom: 16px;
          }

          .label-pasword {
            display: flex;
            justify-content: space-between;
            margin-bottom: 16px;
            align-items: center;
          }

          input {
            font-family: "Inter Medium";

            width: 100%;
            border-radius: 4.5px;
            padding: 10px 12px;
            border: solid 1px #d9dae1;
            font-size: 14px;
          }

          &__input {
            border: solid 1px #d9dae1;
            border-radius: 4.5px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 12px;

            input {
              border: none;
              padding: 10px 0;
            }

            .icon {
              height: 20px;
              cursor: pointer;

              svg {
                width: 20px;
                height: 20px;
              }
              &.active {
                path {
                  fill: $primary;
                }
              }
            }
          }

          .erro-resend {
            display: none;
            color: #f7404e;
            font-size: 11px;
            margin-top: 5px;
          }
          &.erro {
            .erro-resend {
              display: inline;
            }
            input {
              border: 1px solid #f7404e;
              color: #f7404e;

              &::placeholder {
                color: #f7404e;
              }
            }

            .container-inputs__input{
              border: 1px solid #f7404e;

              input{
                border: none;
              }

            }
          }
        }

        button {
          margin-top: 31px;
        }
      }
      .container-login {
        display: none;

        &.active {
          display: block;
        }
      }

      .container-forgot {
        display: none;
        .form-login {
          display: flex;
          flex-direction: column;

          .notification {
            align-self: center;
            max-width: 253px;
            position: absolute;
            top: 73px;
          }

          .btn {
            max-width: 157px;
            font-size: 14px;
          }

          .span-link {
            align-self: center;
            margin-top: 25px;
            font-size: 12px;
            color: #828282;
          }
        }

        &.active {
          display: block;
        }
      }

      .container-reset-password {
        display: none;

        &.active {
          display: block;
        }
      }

      .container-finish {
        display: none;
        img {
          margin-top: 20px;
          width: 100%;
        }

        &.active {
          display: block;
        }
      }
    }
    &__col2 {
      grid-column: 2;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      .title {
        margin-top: 86px;
        font-family: "Inter Bold";
        font-size: 30px;
      }

      .wrapper-anim-col2 {
        width: 512px;
        height: 512px;
        margin-top: 22px;
        img {
          width: 100%;
          height: 100%;
          background: #FFF;
        }
      }
    }
  }
}
