.box-draggable {
  width: 218px;
  min-height: 132px;
  border-radius: 10px;
  border: solid 1px #d9dae1;
  background-color: #fff;
  position: absolute;
  left: 20%;
  top: 20%;


  &__header {
    padding-bottom: 7px;
    font-family: "Inter Regular";
    height: 40px;
    font-size: 11px;
    font-weight: 600;
    color: #828292;
    border-bottom: solid 1px #d9dae1;
    position: relative;
    display: flex;
    justify-content: center;
    &__top {
      position: absolute;
      width: calc(100% - 18px);
      background: #fff;
      bottom: 8px;
      border-radius: 3px;
      padding-top: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        height: 18px;
        width: 18px;
        // position: relative;
        margin-right: 10px;
        path {
          fill: #828292;
        }
      }
    }
  }

  &__main {
      padding: 10px;
      overflow: hidden;



    &__message{
      color: #828292;
      font-size: 11px;
      display: flex;
      align-items: flex-start;
      min-width: 190px;
      max-width: 190px;

    cursor: pointer;
      @include ellipsis(); 
      
      img{
        margin-right: 5px;
      }
    }
    &__connector {
      width: 28px;
      height: 28px;
      border-radius: 4px 0 0 4px;
      background-color: #e9eaef;
      position: absolute;
      display: flex;
      align-items: center;
      cursor: pointer;

      &__item {
        position: relative;
        font-size: 11px;
        right: 25px;
        color: #828292;
      }

      &--right {
        bottom: 10px;
        right: 0;
      }

      &--left {
        left: 0;
        top: 49px;
        border-radius: 0px 4px 4px 0px;
      }

      &--yes {
        bottom: 45px;
        right: 0;
      }

      &--no {
        bottom: 10px;
        right: 0;
      }
    }
  }

  &--status-start {
    box-shadow: 0 3px 13px 2px rgba(32, 33, 38, 0.1);
    border: solid #45c777 1px;
    .box-status {
      background: #45c777;
    }
  }

  &--status-integration {
    box-shadow: 0 3px 13px 2px rgba(32, 33, 38, 0.1);
    border: solid #a14dec 1px;
    .box-status {
      background: #a14dec;
    }
  }

  &--status-location {
    box-shadow: 0 3px 13px 2px rgba(32, 33, 38, 0.1);
    border: solid #53ecca 1px;
    .box-status {
      background: #53ecca;
    }
  }

  &--status-fim {
    box-shadow: 0 3px 13px 2px rgba(32, 33, 38, 0.1);
    border: solid #f7404e 1px;
    .box-status {
      background: #f7404e;
    }
  }
  &--status-condition {
    box-shadow: 0 3px 13px 2px rgba(32, 33, 38, 0.1);
    border: solid #ffa75d 1px;
    .box-status {
      background: #ffa75d;
    }
  }


  &--status-call-agent {
    box-shadow: 0 3px 13px 2px rgba(32, 33, 38, 0.1);
    border: solid #fa4f9b 1px;
    .box-status {
      background: #fa4f9b;
    }
  }
}

.box-status {
  width: 100%;
  height: 20px;
  border-radius: 9px 9px 0px 0px;
}
