.context-menu {
  position: relative;
  border-radius: 5px;
  z-index: 9999;
  overflow: hidden;
  // padding: 1px 2px 1px 0;

  &__list {
    width: 100%;
    // max-height: 600px;
    // margin-right: 10px;
    overflow: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    margin: 0;
  }
}

.container {
  display: flex;
  height: 100%;
  &__menu {
  }

  &__content {
    width: 100%;
    position: relative;

    &__header {
      width: 100%;
    }

    &__body {
      height: calc(100% - 68px);
      padding-top: 21px;
      position: relative;
      overflow-y: auto;

      &--absolute {
        position: absolute;
      }

      &__title {
        font-size: 30px;
        line-height: 37px;
        font-weight: 700;
        color: $primary;
      }
    }

    &__btn-overlay {
      bottom: 30px;
      right: 30px;
      position: absolute;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: 100%;
  height: 61px;
  background: $light-7;
  // box-shadow: 0px 4px 20px #25265e1a;
  border-bottom: solid 1px $light-8;
  z-index: 3;

  &__col {
    display: flex;
    align-items: center;
    height: 100%;
    grid-column: 3;
    grid-row: 1;
  }

  &__logo {
    margin-left: 44px;

    &__img {
      height: 20px;
    }
  }

  &__notification {
    display: flex;
    position: relative;
    align-items: center;
    height: 100%;

    .list-drop {
      width: 303px;
      right: 5px;
      top: 50px;
      cursor: default;

      .title {
        font-family: "Inter SemiBold";
        font-size: 20px;
        margin: 13px 14px 0 14px;
        color: #3e3f44;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 22px;
        border-bottom: 1px solid #d9dae1;

        .icon-Close {
          cursor: pointer;
        }
      }

      &__item {
        height: unset !important;

        padding: 10px 0;
        margin: 0 14px;
        border-bottom: 1px solid #d9dae1;

        &:hover {
          background: #fff;
        }
        &:last-of-type {
          margin-bottom: 10px;
          border-bottom: none;
        }
      }

      .item-notification {
        justify-content: center;
        display: flex;
        width: 34px;
        height: 34px;
        align-items: center;
        border-radius: 4px;
        margin-right: 18px;

        svg {
          margin: 0;
        }

        &.success {
          border: 1px solid #45c777;

          svg {
            path {
              fill: #45c777;
            }
          }
        }
        &.warning {
          border: 1px solid #ffbb00;

          svg {
            path {
              fill: #ffbb00;
            }
          }
        }

        &.error {
          border: 1px solid #f7404e;

          svg {
            path {
              fill: #f7404e;
            }
          }
        }
      }

      .text-notification {
        max-width: 222px;
        font-family: "Inter SemiBold";
        font-size: 12px;
        word-break: break-word;
      }
    }

    &__item {
      margin: 0 14px;
      cursor: pointer;

      svg {
        display: flex;
        max-width: 24px;
        height: 24px;
        path {
          fill: #3e3f44;
          transition: 0.25s;
        }
      }

      // &:last-of-type {
      //   margin-right: 13px;
      //   margin-left: 24px;
      // }
    }
  }
  &__separator {
    width: 2px;
    height: 20px;
    border: solid 1px #e2e2ec;
  }

  &__user {
    display: flex;
    align-items: center;
    margin: 0 46px 0 13px;
    cursor: pointer;
    position: relative;

    .list-drop {
      width: 199px;
      top: 36px;
      z-index: 999;
      right: 0;

      &__item {
        font-family: "Inter SemiBold";
        font-size: 13px;
        color: #3e3f44;
        display: flex;
        align-items: center;
        height: 35px !important;
        transition: all 0.3s;

        svg {
          width: 18px;
          height: 18px;
          margin-right: 13px;
          path {
            fill: #828292;
          }
        }
      }
    }

    &__name {
      font-family: "Inter SemiBold";
      color: #3e3f44;
      font-size: 16px;
      padding: 14px;
      font-weight: 500;
      cursor: default;
      max-width: 170px;
      overflow: hidden;
      white-space: nowrap;
      word-break: break-all;
      text-overflow: ellipsis;
    }

    &__photo {
      width: 100%;
      height: 34px;

      &__img {
        width: 34px;
        height: 34px;
        border-radius: 50px;
        object-fit: cover;
        -webkit-box-shadow: 0px 0px 10px -5px rgba(188, 191, 208, 1);
        -moz-box-shadow: 0px 0px 10px -5px rgba(188, 191, 208, 1);
        box-shadow: 0px 0px 10px -5px rgba(188, 191, 208, 1);
      }
    }

    &__btn {
      display: flex;
      align-items: center;
      cursor: pointer;

      &__name {
        font-size: 16px;
        line-height: 20px;
        margin: 0 5px 0 12px;

        span {
          font-weight: 700;
          color: $primary;
        }
      }

      &__icon {
        display: flex;
        align-items: center;
        .material-icons {
          font-size: 20px;
          color: $light-2;
        }
      }
    }
  }

  &--sub-header {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fbfbff;
    padding: 0 32px;
    font-size: 18px;
    font-family: "Inter Regular";
    border-bottom: 1px solid $light-5;
    box-shadow: none;
    .sub-header-container {
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    .nav-sub-header {
      padding: 8px 4px;
      margin-right: 37px;
      height: 40px;
      @include clickable();

      &:hover {
        border-bottom: 2px solid $primary;
        color: $primary;
      }

      &.active {
        border-bottom: 2px solid blue;
        color: blue;
      }
    }
  }

  &--first-fluxo {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
    background: #fbfbff;
    padding: 0 32px;
    box-shadow: none;
    border-bottom: 1px solid $light-5;
    height: 60px;

    .pdf-export {
      display: none;
    }

    .search {
      display: none;
    }

    .btn-header-fluxo {
      justify-self: end;
      grid-column: 3;
      grid-row: 1;

      .container-btn-fluxo {
        display: flex;
        justify-self: end;

        &--left {
          .help {
          }
          .settings {
          }

          button {
            width: 40px;
            height: 40px;
            padding: 8px 6px;
            margin-left: 17px;
          }
        }

        &--right {
          margin-left: 45px;

          .save {
            width: 100px;
            margin-left: 33px;
          }

          .actions {
            display: flex;
            align-items: center;
            justify-content: space-around;
            color: $primary;
            line-height: 0;
            font-weight: bold;
            border: 2px solid $primary;
          }

          button {
            width: 80px;
            height: 40px;
            margin-left: 10px;
            padding: 0 10px;
            font-size: 14px;
          }
        }
      }
    }
  }
}

.back {
  font-family: "Inter SemiBold";
  display: flex;
  font-size: 20px;
  align-items: center;
  font-size: 20px;
  color: $dark-3;
  i {
    margin-right: 14px;
  }
  @include clickable();
}

.menu {
  background: $primary;
  width: 70px;
  height: 100%;

  &__logo {
    width: 100%;
    height: 60px;
    background: $primary-3;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      width: 33px;
      height: 36px;
      object-fit: cover;
    }
  }

  &__elements {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-top: 152px;

    &__item {
      display: flex;
      justify-content: center;
      width: 100%;
      padding: 20px 26px;
      position: relative;
      cursor: pointer;

      &__icon {
        width: 18px;
        height: 18px;
        z-index: 1;
        svg path {
          fill: $primary-2;
          transition: 0.25s;
        }
      }

      &:hover {
        svg path {
          fill: #fff;
        }
      }

      &.active {
        svg path {
          fill: #fff;
        }

        &::after {
          content: "";
          width: 77px;
          height: 100%;
          border-radius: 0 7px 7px 0;
          background: -moz-linear-gradient(top, #879aed 0%, #6174cf 100%);
          background: -webkit-linear-gradient(top, #879aed 0%, #6174cf 100%);
          background: linear-gradient(to bottom, #879aed 0%, #6174cf 100%);
          transition: 0.25s;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
  }
}
